import React, { useState, useEffect } from 'react';
import logo from '../assets/0xgpt_logoF.svg';


import AOS from 'aos';
import 'aos/dist/aos.css';



const Sidebar = ({children}) => {
    useEffect(() => {
        AOS.init({duration: 1000});
      }, [])

    
    return (
        <div className="container" >
            


           <main>{children}</main>
          
        
        </div>
    );
};

export default Sidebar;